<template>
  <div>
    <el-dialog
      title=""
      width="1000px"
      top="30px"
      :visible.sync="visible"
      :append-to-body="true"
      :close-on-click-modal="false"
      @close="$emit('update:show', false)"
      :show="show"
    >
      <div class="pd-10">
        <el-form :model="crowdxqData" :rules="rules" ref="crowdxqData" class="demo-ruleForm">
          <el-form-item>
            <div class="send-crowd_topic"></div>
          </el-form-item>

          <el-form-item label>
            <el-select
              size="mini"
              v-model="crowdxqData.kfData"
              filterable
              remote
              reserve-keyword
              placeholder="()"
              :remote-method="remoteMethod"
              :loading="loading"
              style="width:100%;"
            >
              <el-option
                v-for="(item,index) in kfgcsList"
                :key="item.uid"
                :label="item.nickName"
                :value="item.uid+','+item.nickName"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label>
            <!-- <div id="summernoteT"></div> -->
            <el-input type="textarea" placeholder="" v-model="crowdxqData.nr"></el-input>
          </el-form-item>

          <el-form-item>
            <div class="send-crowd_topic"></div>
          </el-form-item>
          <el-form-item prop="rwmc">
            <el-input size="mini" v-model="crowdxqData.rwmc" placeholder=""></el-input>
          </el-form-item>

          <!--  -->
          <el-form-item>
            <el-input size="mini" v-model="crowdxqData.xmysje" placeholder="()"></el-input>
          </el-form-item>

          <!--  -->
          <el-form-item prop="xqms">
            <el-input type="textarea" placeholder="" v-model="crowdxqData.xqms"></el-input>
          </el-form-item>

          <el-form-item>
            <div class="send-crowd_topic">//</div>
          </el-form-item>
          <el-form-item>
            <el-date-picker
              :picker-options="pickerJfrqDateBefore"
              value-format="yyyy-MM-dd"
              size="mini"
              type="date"
              placeholder=" "
              v-model="crowdxqData.qwjfrq"
              style="width: 100%;"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <div class="send-crowd_topic" flex spacebetween>
              <span>/</span>
            </div>
          </el-form-item>
          <el-form-item>
            <el-date-picker
              @change="handleChoosejfDate2"
              value-format="yyyy-MM-dd"
              :picker-options="pickerJfrqDateBefore"
              size="mini"
              type="date"
              placeholder=""
              v-model="crowdxqData.jfrq"
              style="width: 100%;"
            ></el-date-picker>
          </el-form-item>

          <el-form-item text-right>
            <el-button size="mini" type="primary" @click="sendSubmitForm('crowdxqData')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import uploadFile from "@/components/BusinessPage/upload.vue";
import { queryUser } from "@/api/personal.js";
import { GetDateStr } from "@/utils/util.js";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (
        !/^([1-9]\d*(\.\d*[1-9])?)|(0\.\d*[1-9])$/.test(value) &&
        crowdxqData.rwlx != "2"
      ) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      dateruleVisible: false, //
      visible: this.show,
      pickerJfrqDateBefore: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
      },
      loading: false, //loading
      kfgcsData: [], //
      kfgcsList: [], //

      rules: {
        rwmc: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        xmysje: [
          {
            validator: validatePass,
            trigger: "blur"
          }
        ],
        zbjzrq: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      crowdxqData: {
        kfData: "",
        rwmc: "", //
        xmysje: 0,
        zbjzrq: "", //
        qwjfrq: "", //
        jfrq: "", //
        nr: ""
      }
    };
  },
  methods: {
    // 
    sendSubmitForm(formName) {
      if (!this.crowdxqData.rwmc) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      if (
        this.crowdxqData.rwlx != "2" &&
        !/^([1-9]\d*(\.\d*[1-9])?)|(0\.\d*[1-9])$/.test(this.crowdxqData.xmysje)
      ) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      if (!this.crowdxqData.xqms) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      if (!this.crowdxqData.jfrq || !this.crowdxqData.zbjzrq) {
        this.$alert("/", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }

      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$emit("handleCommitKfgcs", this.crowdxqData);
        })
        .catch(() => {});
    },

    // 1
    handleChoosejfDate(val) {
      if (val > this.crowdxqData.jfrq) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        this.crowdxqData.zbjzrq = "";
      }
    },
    // 
    handleChoosejfDate2(val) {
      if (val < this.crowdxqData.zbjzrq) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        this.crowdxqData.jfrq = "";
      }
    },

    // 
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        this.queryUser(query);
      } else {
        this.kfgcsList = [];
      }
    },
    // 
    queryUser(keyword) {
      queryUser({
        curPage: 1,
        pageSize: 10,
        Isexternal: false,
        keyword: keyword
      }).then(({ data }) => {
        if (data.state == "success") {
          this.kfgcsList = data.data.rows;
          this.loading = false;
        }
      });
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    show(n, o) {
      this.visible = this.show;
      if (this.show) {
        this.crowdxqData.zbjzrq = GetDateStr(0);
        this.$nextTick(() => {
          $("#summernoteT").summernote({
            dialogsInBody: true,
            placeholder: "",
            focus: true,
            height: 200,
            width: 100 + "%",
            minHeight: 200,
            lang: "zh-CN",
            toolbar: [
              ["style", ["bold", "italic", "underline", "clear"]],
              ["font", ["strikethrough", "superscript", "subscript"]],
              ["fontsize", ["fontsize"]],
              ["color", ["color"]],
              ["para", ["ul", "ol", "paragraph"]],
              ["height", ["height"]],
              ["picture"],
              ["link", ["linkDialogShow", "unlink"]]
            ]
          });
        });
        this.queryUser();
      } else {
        this.crowdxqData.kfData = "";
        this.crowdxqData.rwmc = "";
        this.crowdxqData.xmysje = 0;
        this.crowdxqData.xqms = "";
        this.crowdxqData.zbjzrq = "";
        this.crowdxqData.qwjfrq = "";
        this.crowdxqData.jfrq = "";
        this.crowdxqData.nr = "";
      }
    }
  },
  components: {}
};
</script>

<style scoped>
.send_crowd img {
  width: 100%;
}

.send-crowd_topic {
  border: 1px solid #e4eaec;
  background: #f3f7f9;
  border-radius: 3px;
  height: 40px;
  line-height: 40px;
  padding: 0 6px;
}

.crowd-rqgz h4 {
  font-weight: 700;
  font-size: 13px;
  margin: 10px 0 !important;
}

.crowd-rqgz p {
  font-size: 12px;
  text-indent: 2em;
}
</style>



